.contact {
    &__container {
    // padding : 75px 200px;
    padding: 10% 20%;
    background-color: #1C3B58;
    color: #FFFFFF;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 270px;
    }
}

.contact-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    width: 100%;
    h2 {
        margin: 0;
        padding: 0;
        font-size: 40px;
        font-weight: 600;
        text-align: center;
    }
    p {
        text-align: center;
        font-size: 16px;
        font-weight: 400;
        margin: 0;
        padding: 0;
    }
    .contact-btn {
        background-color: #6FC0CB;
        padding: 12px 24px;
        font-size: 16px;
        font-weight: 500;
        margin: 0;
        cursor: pointer;
        border-radius: 6px;
        color: #FFFFFF;
        border: none;
        &:hover {
            color: #6FC0CB;
            background-color: #FFFFFF;
        }
    }
    .go-back {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        button {
            background: none;
            color: white;
            border: none;
            font-size: 14px;
            &:hover {
                color: #6FC0CB;
            }
        }
    }
    .social-links {
        height: 24px;
        a {
            height: 100%;
        }
        img {
            height: 100%;
            width: 100%;
        }
    }
}