@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

body {
    font-family: 'Poppins', sans-serif;
    color: #1C3B58;
    margin: 0;
    padding: 35px 130px;
    @media (max-width: 768px) {
        padding: 30px 60px;
    }
    @media (max-width: 480px) {
        padding: 25px 40px;
    }
    &.dark {
        background-color: #161625;
        color: #FFFFFF;
        a {
            color: white;
            // &:hover {
            //     color: #818cab;
            // }
        }
        h3 {
            color: white;
        }
        .project-card {
            background-color: white;
            color: #161625;
            h3 {
                color: #818cab;
            }
        }
        .tag-list-wrapper {
            li {
            border: 1.2px solid #818cab;
                &:hover {
                    color: #818cab;
                }
            }
            .tag-item.active {
                background-color: #818cab;
            }
        }
        // .tags-list li {
        //     border: white;
        // }
        .accordion__title {
            background-color: #818cab;
        }
        .contact__container {
            background-color: #818cab;
        }
        .cv-btn {
            background-color: #818cab;
            &:hover {
                color: #161625;
            }
        }
        .contact-btn {
            background-color: white;
            color: #818cab;
            &:hover {
                color: #161625;
            }
        }
        .back-to-top {
            background-color: #818cab;
        }
        .go-back {
            color: #818cab;
            &:hover {
                color: white;
            }
        }
        .slideshow__navigation button {
            background-color: #818cab;
        }
        .project__links a {
            background-color: #818cab;
            &:hover {
                color: #161625;
            }
        }
        .slideshow__circle.active {
            background-color: #818cab;
        }
        .error a {
            background-color: #818cab;
        }
    }
}

